<template>
  <div class="style-editing-panel" v-if="activeComponentID">
    <el-tabs stretch>
      <el-tab-pane label="内容" name="0">
				<div class="menu-wrapper">
          <div class="bg-music-select">
            <span>作为背景音乐</span>
            <a-switch defaultChecked  />
          </div>
          <div class="select-music">
            <span>当前音频:</span>
            <div class="select-music-button">
              <img src="@/assets/icon/upload_music.png" alt="">
              <span>选择音频</span>
            </div>
          </div>
				</div>
	      </el-tab-pane>
      <el-tab-pane label="样式" name="1">
								<div class="menu-wrapper"> 
									<background-change :defaultColor="color" />
									<slider :value="opacity" :opts="{placeholder: '透明度',inputBox: true,hasRate: true}" @valueChange="opacityChange" />
									  <a-divider  />

					<margin-edit  @valueChange="marginChange"/>
					
				</div>

			</el-tab-pane>
      <el-tab-pane label="动画" name="2"></el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import slider from "@/components/StyleMenuComponents/slider.vue"
import backgroundChange from "@/components/StyleMenuComponents/backgroundChange.vue"
import textEditor from "@/components/StyleMenuComponents/textEditor.vue"
import MarginEdit from "@/components/StyleMenuComponents/marginEdit.vue";
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState, mapGetters } = createNamespacedHelpers(
  "componentStore"
);
export default {
  components: {
    "margin-edit": MarginEdit,
		"background-change": backgroundChange,
		 "slider":slider
  },
  methods: {
    ...mapActions(["updateComponentInfo"]),
		marginChange(message){
			let payload = {
				id: this.activeComponentID,
				value:{
					style:{
						[message.key]: `${message.value}px`
					}
				}
			};
			this.updateComponentInfo(payload);
		},
		opacityChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          style: {
            opacity: value / 100
          }
        }
      };
      this.updateComponentInfo(payload);
    }
  },
  computed: {
    ...mapState(["activeComponentID"]),
    ...mapGetters(["activatedComponent"]),
    color:{
			get(){
				return this.activatedComponent.componentInfo.style["background-color"]
			}
		},
		opacity:{
			get(){
				return this.activatedComponent.componentInfo.style.opacity * 100
			}
		}
  },
  mounted() {}
};
</script>

